export const PANEL_HOME = 'home';
export const PANEL_TASKS = 'tasks';
export const PANEL_RULES = 'rules';
export const PANEL_PRIZES = 'prizes';
export const PANEL_JURY = 'jury';

function getPanelList() {
    return [
        PANEL_HOME,
        PANEL_TASKS,
        PANEL_RULES,
        PANEL_PRIZES,
        PANEL_JURY
    ]
}

export function getActivePanel(pathname) {
    if (pathname === "/") {
        return PANEL_HOME
    }
    let panel = pathname.replace("/", "");
    return getPanelList().indexOf(panel) !== -1 ? panel : PANEL_HOME;
}



