import React from 'react';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import connect from '@vkontakte/vkui-connect';
import Home from './panels/Home';
import Tasks from './panels/Tasks';
import Rules from './panels/Rules';
import Prizes from './panels/Prizes';
import Jury from './panels/Jury';
import {getActivePanel, PANEL_HOME, PANEL_JURY, PANEL_PRIZES, PANEL_RULES, PANEL_TASKS} from "./routes"
import {PanelHeader, HeaderButton, platform, IOS} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();

connect.subscribe((e) => {
	if (Object.keys(e.detail).length > 0) {
		switch (e.detail.type) {
			case 'VKWebAppUpdateConfig':
				document.body.setAttribute("scheme", e.detail.data.scheme);
			break;
			default:
		}
	}
})

class App extends React.Component {

	go = (e) => {
		let nextPage = e.currentTarget.dataset.to
		if (nextPage !== this.props.location.pathname.replace("/", "")) {
        this.props.history.push(e.currentTarget.dataset.to)
    }
	};

	back = () => {
		this.props.history.goBack()
	};

	renderPanelHeader = (title) => {
        return <PanelHeader
            noShadow
            addon={<HeaderButton onClick={this.back}>Назад</HeaderButton>}
            left={<HeaderButton onClick={this.back}>
                {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
            </HeaderButton>}>
          {title}
        </PanelHeader>
	}

	render() {
		return (
			<View activePanel={getActivePanel(this.props.location.pathname)}>
				<Home id={PANEL_HOME} go={this.go} />
				<Tasks id={PANEL_TASKS} go={this.go} panelHeader={this.renderPanelHeader("Задания")}/>
				<Rules id={PANEL_RULES} go={this.go} panelHeader={this.renderPanelHeader("Правила")}/>
				<Prizes id={PANEL_PRIZES} go={this.go} panelHeader={this.renderPanelHeader("Призы")}/>
				<Jury id={PANEL_JURY} go={this.go} panelHeader={this.renderPanelHeader("Жюри")}/>
			</View>
		);
	}
}

export default App;



