import React from 'react';
import PropTypes from 'prop-types';
import {Panel, Group, InfoRow, List, Cell, Footer} from '@vkontakte/vkui';

const Prizes = props => (
  <Panel id={props.id}>
    {props.panelHeader}
    <Group>
      <List>
        <Cell>
          <InfoRow title="Первое место">
            Смартфон iPhone XS Max, 512 ГБ, цвет «Серый космос»
          </InfoRow>
        </Cell>
        <Cell>
          <InfoRow title="Второе место">
            Экшн-камера GoPro Hero 7 Black + стабилизатор-монопод для смартфона DJI Osmo Mobile 2
          </InfoRow>
        </Cell>
        <Cell>
          <InfoRow title="Третье место">
            Экшн-камера GoPro Hero 7 Black
          </InfoRow>
        </Cell>
      </List>
    </Group>
    <Footer>3 приза</Footer>
  </Panel>
);

Prizes.propTypes = {
  id: PropTypes.string.isRequired,
  go: PropTypes.func.isRequired,
  panelHeader: PropTypes.object.isRequired,
};

export default Prizes;



