import React from 'react';
import PropTypes from 'prop-types';
import {Panel, Button, FixedLayout, Div} from '@vkontakte/vkui';

const Tasks = props => (
	<Panel id={props.id} theme="white">
		{props.panelHeader}
		<div className="TasksImage"/>
		<FixedLayout vertical="bottom">
			<Div>
				<Button size="xl" level="secondary" component="a" href="https://vk.com/photo-155639064_456239017">
					Открыть в полном размере
				</Button>
			</Div>
		</FixedLayout>
	</Panel>
);

Tasks.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	panelHeader: PropTypes.object.isRequired,
};

export default Tasks;



